    padding: 0rem 2.5rem 0rem 2.5rem;
  }
  .is--ctl-index .content-main--inner {
    padding: 0rem 1.25rem 0rem 1.25rem;
  }
  .emotion--wrapper {
    max-width: 78.75em;
  }
  .shop--navigation .cart--display {
    display: inline;
  }
  .icontext {
    display: inline-block;
  }
  .icontext.shipping {
    margin-left: 11.25rem;
  }
  .content--breadcrumb {
    padding-left: 1.75rem;
  }
  .top-bar--navigation .navigation--entry {
    margin-right: 1.25rem;
  }
  .header-main .top-bar--navigation .top-bar--currency {
    margin-left: 1.25rem;
  }

  .steps--container {
    padding-left: 1.875rem;
  }
  .is--minimal-header .header--navigation {
    padding-left: 2.5rem;
  }
  html[lang="de"] .navigation-main .navigation--link {
    padding: 0.6875rem 3.75rem 0.6875rem 3.75rem;
  }
  .banner-overlay {
    max-width: 23.125rem;
    font-size: 1.875rem;
    padding: 2.5rem 5.625rem 0rem 1.875rem;
  }
  .buybox--inner .priceWrapper {
    width: 45%;
  }
  .page-wrap--cookie-permission {
    padding: 0.625rem 1.5625rem 0.625rem 1.5625rem;
  }
  .js--img-zoom--flyout {
    width: 31rem;
    right: -33.9375rem;
  }
  .image-slider--thumbnails {
    width: 100%;
    margin: 0;
    position: relative;
    float: none;
  }
  .product--detail-upper .image-slider--container {
    margin-bottom: 0.75rem;
    height: 40rem;
  }
  .image-slider--thumbnails .thumbnail--link {
    display: inline-block;
  }
  .image-slider--dots {
    display: none;
  }
  .confirm--content {
    padding-top: 0;
  }
  .steps--content .steps--entry .text {
    margin-left: 0.5625rem;
  }
  _:-ms-fullscreen,
  :root .shipping.icontext {
    margin-left: 170px;
  }
  #storeLocator #searchWrap #searchOptions #storeSelect {
    width: auto !important;
  }
  #storeLocator #searchWrap #searchOptions input {
    width: 20em !important;
  }
}
@media screen and (min-width: 87.5rem) {
  .laschen {
    display: block;
  }
}
@media print {
  .ccMobileMessage,
  .header-main .logo-main,
  .cc--detail-shipping-costs,
  .shipping-costs--price,
  .product--configurator,
  .buybox--form,
  nav.product--actions,
  .youtube.bottombuybox,
  .product--buybox.reviews,
  .youtubeouter,
  .tab-menu--cross-selling.js--tab-menu,
  .footer-main,
  .last-seen-products,
  cookie-permission--container,
  .topstoerer {
    display: none;
  }
}
